import React, { useState } from 'react';
import emailjs from 'emailjs-com';

const ContactForm = () => {
  const [formData, setFormData] = useState({
    from_name: '',
    email: '',
    message: ''
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    emailjs.send('service_lerd2nt', 'template_o32okyf', formData, 'eUj2gowei543KlPRE')
      .then((response) => {
        console.log('SUCCESS!', response.status, response.text);
        alert('감사합니다! 확인하는 대로 최대한 빠르게 회신드리겠습니다.');
      }, (error) => {
        console.log('FAILED...', error);
        alert('전송에 실패했습니다.');
      });
  };

  return (
    <article id="form_box">
        <div className="inner">
            <div className="form_top">
                <div className="tit">메시지</div>
                <div className="desc">
                    함께 일할 수 있는 멋진 역량과 경험을 가진 사람이 필요하신가요? <br />
                    저와 나누고 싶으신 말씀이 있으시다면 편하게 연락 주세요:) <br />
                    확인하는 대로 최대한 빠르게 회신드릴게요.
                </div>
            </div>
        </div>
        <form onSubmit={handleSubmit}>
            <ul className="form_list">
                <li>
                    <label htmlFor="form_name" className="tit">성함</label>
                    <div className="cont">
                        <input id="form_name" type="text" name="from_name" value={formData.from_name} onChange={handleChange} placeholder="성함" required />
                    </div>
                </li>
                <li>
                    <label htmlFor="form_email" className="tit">연락 받으실 연락처 or 이메일</label>
                    <div className="cont">
                        <input id="form_email" type="text" name="email" value={formData.email} onChange={handleChange} placeholder="연락 받으실 연락처 or 이메일" required />
                    </div>
                </li>
                <li>
                    <label htmlFor="form_message" className="tit">메시지</label>
                    <div className="cont">
                        <textarea id="form_message" name="message" value={formData.message} onChange={handleChange} placeholder="메시지" required></textarea>
                    </div>
                </li>
            </ul>
            <div className="form_btn"><button type="submit">보내기</button></div>
        </form>
    </article>
  );
};

export default ContactForm;
